.tr:nth-child(even){background-color:rgba(1,97,95,.6)}.tr:nth-child(odd){background-color:#01615f}.tr .td{color:#fff;font-family:var(--font-roboto);font-weight:500;font-size:24px;padding:10px}.tr .td.managePupils{display:flex;gap:10px}.tr .td.managePupils .option{display:flex;background-color:hsla(0,0%,100%,.5);padding:5px;border-radius:5px;transition:all .3s}.tr .td.managePupils .option:hover{transform:scale(1.2)}