.table{border:none;width:100%}.table .thead{border-bottom:2px solid #000}.table .thead .tr .th{text-align:start;font-size:24px;padding:10px 10px 20px 10px;text-transform:uppercase;font-weight:500;font-family:var(--font-roboto)}.table .tbody .tr:nth-child(even){background-color:rgba(1,97,95,.6)}.table .tbody .tr:nth-child(odd){background-color:#01615f}.table .tbody .tr .td .consult-pupile:hover{background-color:rgba(0,0,0,0);border-color:#fff}.table .tbody .tr .td .consult-pupile:hover img{filter:brightness(0) invert(1)}.table .tbody .tr.add-pupil{background-color:#7683d3}.table .tbody .tr.add-pupil .add-pupil-content{cursor:pointer}.table .tbody .tr.add-pupil .add-pupil-content{text-transform:uppercase;display:flex;align-items:center;background-color:#fff;color:#7683d3;width:fit-content;padding:5px 10px;border-radius:5px;transition:all .3s;min-width:150px;overflow:hidden;border:none;outline:none}.table .tbody .tr.add-pupil .add-pupil-content .add-pupil-plus{font-size:28px;transition:font-size .3s}.table .tbody .tr td{padding:10px;color:#fff;font-size:24px}.table .tbody .tr td.select{padding:10px 6px}.table .tbody .tr td .pupil-panel{display:flex;gap:10px}.table .tbody .tr td .consult-pupile{padding:5px;display:flex;background-color:#fff;border-radius:5px;width:fit-content;cursor:pointer;transition:background-color .3s;border:1px solid rgba(0,0,0,0)}.table .tbody .tr td .consult-pupile img{width:20px;height:20px;object-fit:contain;transition:all .3s}.table .tbody .tr td input{border:none;outline:none;background-color:rgba(0,0,0,0);font-size:24px;color:#fff;padding:0}.table .tbody .tr td input::placeholder{color:hsla(0,0%,100%,.8)}.table .tbody .tr td select{outline:none;border:none;cursor:pointer;background:none;color:#fff;font-size:24px;padding:0;font-family:"Oswald";padding:0;margin:0}.table .tbody .tr td select option{color:#000}